.user-rankings {
  text-align: center;
  padding: 60px 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.main-title {
  font-size: 2.2rem;
  font-weight: bold;
  color: white;
  margin-bottom: 10px;
}

.subtitle {
  font-size: 1.1rem;
  color: wheat;
  margin-bottom: 50px;
}

.ranking-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
  max-width: 1100px;
  margin: 0 auto;
  text-align: left;
}

.ranking-image img {
  max-width: 450px;
  width: 100%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

.ranking-content {
  max-width: 500px;
}

.ranking-title {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 15px;
  color: white;
}

.ranking-desc {
  font-size: 1rem;
  color: white;
  line-height: 1.6;
  margin-bottom: 25px;
}

.ranking-btn {
  padding: 12px 24px;
  background-color: #102d6dff;
  color: #fff;
  font-weight: bold;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s ease;
  text-decoration: none;
}

.ranking-btn:hover {
  background-color: #4a2ea8;
}

/* Responsive */
@media (max-width: 768px) {
  .ranking-section {
    flex-direction: column;
    text-align: center;
  }

  .ranking-content {
    max-width: 100%;
  }

  .ranking-title {
    text-align: center;
  }

  .ranking-desc {
    text-align: center;
  }
}
